import "./styles.css";
import Create from "./Components/Create";
import Read from "./Components/Read";
import Update from "./Components/Update";
import Post from "./post";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import axios from "axios";
import Get from "./get";
import Put from "./put";
function App() {
  return (
    <div className="main">
      <div className="boxflex">
        <div>
          <Create />
        </div>
        <div>
          <Read />
        </div>
      </div>
      <div>
        <Update />
      </div>
      <div>
        <BrowserRouter>
          <Routes>
            <Route excat path="/Post" element={<Post />}></Route>
            <Route excat path="/Get" element={<Get />}></Route>
            <Route excat path="/Put" element={<Put />}></Route>
          </Routes>
        </BrowserRouter>
      </div>
    </div>
  );
}

export default App;
